<template>
  <div>
    <h3 class="text-center p-3">
      COVID-19 Realtime Data Monitor
    </h3>

    Update Terakhir:
    <span class="bg-primary pl-2 pr-2 text-white">
      <span v-if="worldStat.length == 1">memuat...</span>
      <span v-else>{{worldStat.statistic_taken_at}}</span>
    </span>

    <div class="bg-white p-3 shadow-sm mb-2 mt-3" v-if="countryStat.length > 0">
      <div class="row">
        <div class="col-md-6">
          <b style="font-size:30px">Kasus Per Negara</b>
        </div>
        <div class="col-md-6">
          <select class="form-control" @change="changeCountry">
            <option v-for="(c,index) in countryStat" :key="index" :selected="c.country_name==='Indonesia'?true:false">{{c.country_name}}</option>
          </select>
        </div>
      </div>
      <hr>
      <table class="table text-center">
        <tr>
          <th class="bg-warning text-white">Terinfeksi</th>
          <th class="bg-success text-white">Sembuh</th>
          <th class="bg-danger text-white">Kematian</th>
        </tr>
        <tr v-if="statusCountry == true">
          <td>{{firstCountry[0].cases}}</td>
          <td>{{firstCountry[0].total_recovered}}</td>
          <td>{{firstCountry[0].deaths}}</td>
        </tr>
        <tr v-else>
          <td>{{countryStat[selectedCountry].cases}}</td>
          <td>{{countryStat[selectedCountry].total_recovered}}</td>
          <td>{{countryStat[selectedCountry].deaths}}</td>
        </tr>
      </table>
      <table class="table">
        <tr>
          <td>Terinfeksi Baru</td>
          <td>:</td>
          <td v-if="statusCountry == true"><span class="bg-warning text-white p-2 rounded">{{firstCountry[0].new_cases}}</span></td>
          <td v-else><span class="bg-warning text-white p-2 rounded">{{countryStat[selectedCountry].new_cases}}</span></td>
        </tr>
        <tr>
          <td>Kematian Baru</td>
          <td>:</td>
          <td v-if="statusCountry == true"><span class="bg-danger text-white p-2 rounded">{{firstCountry[0].new_deaths}}</span></td>
          <td v-else><span class="bg-danger text-white p-2 rounded">{{countryStat[selectedCountry].new_deaths}}</span></td>
        </tr>
        <tr>
          <td>Terpantau</td>
          <td>:</td>
          <td v-if="statusCountry == true"><span class="bg-secondary text-white p-2 rounded">{{firstCountry[0].serious_critical}}</span></td>
          <td v-else><span class="bg-secondary text-white p-2 rounded">{{countryStat[selectedCountry].serious_critical}}</span></td>
        </tr>
      </table>
    </div>
    <div v-else class="bg-white p-3 shadow-sm mb-2 mt-3">Memuat Data...</div>

    <div v-if="worldStat.length == 1" class="bg-white p-3 shadow-sm mb-2 mt-3">Memuat Data...</div>
    <div class="bg-white p-3 shadow-sm mb-2" v-else>
      <b style="font-size:30px">Kasus Global</b>
      <hr>
      <table class="table text-center">
        <tr>
          <th class="bg-warning text-white">Terinfeksi</th>
          <th class="bg-success text-white">Sembuh</th>
          <th class="bg-danger text-white">Kematian</th>
        </tr>
        <tr>
          <td>{{worldStat.total_cases}}</td>
          <td>{{worldStat.total_recovered}}</td>
          <td>{{worldStat.total_deaths}}</td>
        </tr>
      </table>
      <table class="table">
        <tr>
          <td>Terinfeksi Baru</td>
          <td>:</td>
          <td><span class="bg-warning text-white p-2 rounded">{{worldStat.new_cases}}</span></td>
        </tr>
        <tr>
          <td>Kematian Baru</td>
          <td>:</td>
          <td><span class="bg-danger text-white p-2 rounded">{{worldStat.new_deaths}}</span></td>
        </tr>
      </table>
      <hr>
      <b style="font-size:20px">Peta Penyebaran Virus:</b>
      <iframe src="https://gisanddata.maps.arcgis.com/apps/Embed/index.html?webmap=14aa9e5660cf42b5b4b546dec6ceec7c" style="width:100%;height:400px;border:1px solid #DDD"></iframe>
    </div>

    <div class="text-center small p-3">
      #StaySafe, ArsanDev
      <br>
      Data From @RapidAPI / Corona Monitor
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        worldStat: ['0'],
        countryStat: [],
        selectedCountry: '',
        firstCountry:'',
        statusCountry: true
      }
    },
    methods: {
      GetSortOrder(prop) {
        return function(a, b) {
            if (a[prop] > b[prop]) {
                return 1;
            } else if (a[prop] < b[prop]) {
                return -1;
            }
            return 0;
        }
      },
      ready(){
        this.axios.get('https://coronavirus-monitor.p.rapidapi.com/coronavirus/worldstat.php', {
         headers: {
           "X-RapidAPI-Host": 'coronavirus-monitor.p.rapidapi.com',
           "X-RapidAPI-Key": '46b28fbbb6mshee531a44c791e7dp1d124djsnecdee99579a9'
         }
       }).then(res=>{
         this.worldStat = res.data
       })
       this.axios.get('https://coronavirus-monitor.p.rapidapi.com/coronavirus/cases_by_country.php', {
         headers: {
           "X-RapidAPI-Host": 'coronavirus-monitor.p.rapidapi.com',
           "X-RapidAPI-Key": '46b28fbbb6mshee531a44c791e7dp1d124djsnecdee99579a9'
         }
       }).then(res=>{
         this.countryStat = res.data.countries_stat.sort(this.GetSortOrder("country_name"))
         this.firstCountry = this.countryStat.filter(obj => {
          return obj.country_name === 'Indonesia'
        })
       })
     },
     changeCountry(e){
       this.selectedCountry = e.target.selectedIndex
       this.statusCountry = false
     }
    },
    mounted() {
      this.ready()
    }
  }
</script>
